import React, { useState } from 'react';
import '../../App.css';

const handleSubmitNames = ( e, names, setNames, nameInput, setInput, email, setEmail, emailInput, setEmailInput) => {
  e.preventDefault()
  setNames([...names, nameInput])
  setInput('')
  setEmail([...email, emailInput])
  setEmailInput('')
}


function Contactanos () {
  const [names, setNames] = useState([])
  const [nameInput, setInput] = useState('')

  const [emailInput, setEmailInput] = useState('')
  
  return (
    <div>
       <h1 className='development'>Contactanos</h1>
      <div className='lorems'>
        <h1>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Quis, obcaecati.</h1>
        <h2>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Sunt esse labore vitae vel odio sapiente reiciendis ratione veritatis impedit iste reprehenderit error deleniti nulla fugit, ipsa, iure temporibus rem maxime.</h2>
      </div>
          <form className='contactusform' onSubmit={(e) => handleSubmitNames(e, names, setNames, setEmailInput)}>
            <label className='labels'>
            Name
            </label>
            <input className='inputclass' onChange={(e) => setInput(e.target.value)} value={nameInput} />
            <label className='labels'>
            Email
            </label>
            <input className='inputclass' onChange={(e) => setEmailInput(e.target.value)} value={emailInput} />
            <textarea placeholder=' Escribenos tu mensaje .... Por favor incluye tu telefono para contactarnos contigo' className='textclass' />
      </form>
    </div>
  );
}

export default Contactanos
